import { useDynamicAlbumsContext } from "contexts/DynamicAlbumsContext";
import { useParams } from "react-router-dom";
import { Gallery } from "react-ikusi";
import { useRef } from "react";

const DEFAULT_CONFIGURATIONS = [
    { minWidth: 1024, cols: 7, margin: 3 },
    { minWidth: 480, maxWidth: 1023, cols: 7, margin: 1 },
    { maxWidth: 479, cols: 4, margin: 1 },
];

export const DynamicAlbum = () => {
    const { albumName } = useParams<{ albumName: string }>();
    const { albums, isLoading, isAlbumsFailed } = useDynamicAlbumsContext();
    const ref = useRef<HTMLDivElement>(null);

    const photos = albumName ? albums[albumName] || [] : [];
    
    const showContent = !isLoading && photos.length > 0;

    return (
        <div ref={ref} className={`page-with-gallery ${isLoading ? 'page-with-gallery--loading' : ''} ${showContent ? 'page-with-gallery--loaded' : ''}`}>
            {isLoading && (
                <div className="page-with-gallery__loading">
                    <div className="page-with-gallery__spinner"></div>
                </div>
            )}
            {isAlbumsFailed && !isLoading && (
                <div className="page-with-gallery__error">
                    <p>Failed to load albums. Please refresh the page.</p>
                </div>
            )}
            {!isLoading && !isAlbumsFailed && photos.length === 0 && (
                <div className="page-with-gallery__error">
                    <p>Album not found or empty.</p>
                </div>
            )}
            {showContent && (
                <Gallery
                    photos={photos}
                    configurations={DEFAULT_CONFIGURATIONS}
                />
            )}
        </div>
    );
};

