import { createContext, useContext, ReactNode } from 'react';
import { TransformedPhotoProps } from 'utils/transform-flickr-result';

interface DynamicAlbumsContextType {
    albums: Record<string, TransformedPhotoProps[]>;
    albumNames: string[];
    isLoading: boolean;
    isAlbumsFailed: boolean;
}

const DynamicAlbumsContext = createContext<DynamicAlbumsContextType | undefined>(undefined);

interface DynamicAlbumsProviderProps {
    children: ReactNode;
    albums: Record<string, TransformedPhotoProps[]>;
    albumNames: string[];
    isLoading: boolean;
    isAlbumsFailed: boolean;
}

export const DynamicAlbumsProvider = ({ 
    children, 
    albums, 
    albumNames, 
    isLoading, 
    isAlbumsFailed 
}: DynamicAlbumsProviderProps) => {
    return (
        <DynamicAlbumsContext.Provider value={{ albums, albumNames, isLoading, isAlbumsFailed }}>
            {children}
        </DynamicAlbumsContext.Provider>
    );
};

export const useDynamicAlbumsContext = () => {
    const context = useContext(DynamicAlbumsContext);
    if (context === undefined) {
        throw new Error('useDynamicAlbumsContext must be used within a DynamicAlbumsProvider');
    }
    return context;
};

