import { useQuery } from '@tanstack/react-query';
import { getDynamicAlbums } from 'utils/FlickrAPI';
import { SizeKeys } from 'utils/constants';
import { TransformedPhotoProps, transformToAlbum } from 'utils/transform-flickr-result';

const defDefault = SizeKeys.medium800;
const bigDefault = SizeKeys.large;

interface useDynamicAlbumsProps {
    shouldFetch?: boolean,
    def?: SizeKeys,
    big?: SizeKeys
}

export const useDynamicAlbums = ({
    shouldFetch = true,
    def = defDefault,
    big = bigDefault,
}: useDynamicAlbumsProps = {}) => {
    const params = [
        `url${def}`,
        `url${big}`,
    ];

    // Create a unique cache key for this photo query
    const queryKey = ['dynamic-albums'];

    const fetchAlbums = async (): Promise<Record<string, TransformedPhotoProps[]>> => {
        const result = await getDynamicAlbums(params);
        const transformed = transformToAlbum(result, def, big);
        return transformed;
    };

    const query = useQuery({
        queryKey,
        queryFn: fetchAlbums,
        enabled: shouldFetch,
        staleTime: 20 * 60 * 1000, // 20 minutes for photo galleries
        gcTime: 2 * 60 * 60 * 1000, // 2 hours in memory
        retry: (failureCount, error) => {
            // Don't retry if it's a clear API error
            if (error.message.includes('Failed to fetch albums')) {
                return failureCount < 1;
            }
            return failureCount < 3;
        },
    });

    return {
        albums: query.data || {},
        albumNames: query.data ? Object.keys(query.data) : [],
        isLoading: query.isLoading,
        isAlbumsFailed: query.isError,
        error: query.error,
        refetch: query.refetch,
    };
};

