import { AppLanguage, DEFAULT_LANGUAGE, isAppLanguage } from "app.constants";
import { Suspense, useEffect, useState } from "react";
import { BrowserRouter, Route, Navigate, useLocation } from "react-router-dom";
import { Routes } from "@datadog/browser-rum-react/react-router-v6";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { buildRoute, buildRoutes } from "router/Routes";
import { BASE_PATH, ROUTES, DYNAMIC_ALBUM_ROUTE } from "router/router.constants";
import './i18n/i18n';
import { AppLayout } from "AppLayout/AppLayout";
import { useTranslation } from "react-i18next";
import { useLocaleFromPathname } from "hooks/useLocaleFromPathname";
import { useCurrentUser } from "hooks/useCurrentUser";
import { useRef } from "react";
import { startNewView } from "instrument";
import { useDynamicAlbums } from "hooks/useDynamicAlbums";
import { DynamicAlbumsProvider } from "contexts/DynamicAlbumsContext";
interface previousNavStateProps {
  pathName: string,
}

// Create a client
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      staleTime: 15 * 60 * 1000, // 15 minutes - photos don't change often
      gcTime: 60 * 60 * 1000,    // 1 hour - keep in memory
      refetchOnWindowFocus: false, // Don't refetch when window gains focus
      retry: 2, // Retry failed requests twice
      refetchOnReconnect: true, // Refetch when internet reconnects
    },
  },
});

const LocalizedRouter = () => {
  const { i18n } = useTranslation();
  const locale = useLocaleFromPathname();
  const [currentLanguage, setCurrentLanguage] = useState<AppLanguage>(locale as AppLanguage);

    useEffect(() => {
        i18n.changeLanguage(isAppLanguage(locale) ? locale : DEFAULT_LANGUAGE);
    }, [i18n, locale]);

    const updateLanguage = (lang: AppLanguage) => {
      i18n.changeLanguage(lang as AppLanguage);
      setCurrentLanguage(lang);
    }

  useCurrentUser();  // Handle user details in RUM

  const location = useLocation();
  const previousPathRef = useRef<previousNavStateProps>({ pathName:''}); // we need to remember the previous path and page
 
  useEffect(() => { 
    // If the previous and the current states are different, we start a new view
    if (previousPathRef.current.pathName !== location.pathname) { 
      startNewView(location.pathname);
    }
    // Update the ref with the current state of the navigation 
    previousPathRef.current = { pathName: location.pathname }; 
  }, [location]); 

  const { albums, albumNames, isLoading, isAlbumsFailed } = useDynamicAlbums();

  return (
    <DynamicAlbumsProvider 
      albums={albums} 
      albumNames={albumNames} 
      isLoading={isLoading} 
      isAlbumsFailed={isAlbumsFailed}
    >
      <div>
        <AppLayout setLanguage={updateLanguage} language={currentLanguage} dynamicAlbumNames={albumNames} isLoadingAlbums={isLoading}>
          <Routes>
            <Route key="root" path={BASE_PATH}>
              {buildRoute(ROUTES.HOME_ROUTE)}
              {buildRoutes(ROUTES.GATZA_ROUTES)}
              {buildRoutes(ROUTES.ARAOTZ_ROUTES)}
              {buildRoutes(ROUTES.BESTALDETIK_ROUTES)}
              {buildRoute(DYNAMIC_ALBUM_ROUTE)}
              {buildRoute(ROUTES.ERROR_ROUTE)}
            </Route>
            <Route path='*' element={<Navigate to={`/${DEFAULT_LANGUAGE}`} />} />
          </Routes>
        </AppLayout>
      </div>
    </DynamicAlbumsProvider>
  );
}

const App = () => (
  <QueryClientProvider client={queryClient}>
    <Suspense fallback={<div>suspense fallback</div>}>
      <BrowserRouter basename="/atauri">
          <LocalizedRouter />
      </BrowserRouter>
    </Suspense>
  </QueryClientProvider>
);

export default App;
