import { PhotoProps } from 'react-ikusi';
import { SizeKeys } from './constants';

const urlKey = (key: string) => `url${key}`;
const widthKey = (key: string) => `width${key}`;
const heightKey = (key: string) => `height${key}`;

const urls: string[] = [];
const sizes: string[] = [];
Object.values(SizeKeys).forEach((k: string) => {
  urls.push(urlKey(k));
  sizes.push(widthKey(k));
  sizes.push(heightKey(k));
});

export type FlickrResult = { [K in typeof urls[number]]: string }
  & { [K in typeof sizes[number]]: number }
  & {
    id: string;
    title: string;
    tags?: string;
  };

/**
 * Transforms Flickr result to Photo to be displayed inside Gallery
 * @param result FlickrAPI result data
 * @param def Default picture object
 * @param big Big picture object
 * @returns Serialized Photo
 */

export interface TransformedPhotoProps extends PhotoProps {
  tag?: string;
}

const transformSinglePhoto = (result: FlickrResult, urlAttribute:string, widthAttribute:string, heightAttribute:string, bigUrlAttribute:string): TransformedPhotoProps => {
  return {
    src: result[urlAttribute],
    width: result[widthAttribute],
    height: result[heightAttribute],
    bigSrc: result[bigUrlAttribute],
    id: result.id,
    title: result.title,
    tag: result.tags,
  };
};

export const transformToPhoto = (result: FlickrResult[] | FlickrResult, def: SizeKeys, big: SizeKeys): TransformedPhotoProps | TransformedPhotoProps[] => {
  // calculate attribute names to get information from result
  const urlAttribute = urlKey(def);
  const widthAttribute = widthKey(def);
  const heightAttribute = heightKey(def);
  const bigUrlAttribute = urlKey(big);
  if(Array.isArray(result)) {
    return result.map((r) => transformSinglePhoto(r, urlAttribute, widthAttribute, heightAttribute, bigUrlAttribute));
  } else {
    return transformSinglePhoto(result, urlAttribute, widthAttribute, heightAttribute, bigUrlAttribute);
  }
};

export const transformToAlbum = (result: FlickrResult[], def: SizeKeys, big: SizeKeys): Record<string, TransformedPhotoProps[]> => {
    const albums = new Map();
    result.forEach((item) => {
        const albumName = item?.tags?.split(' ').find((tag: string) => tag?.includes('atauri:album='))?.split('=')[1];
        if(albumName && albums.has(albumName) && item) {
            albums.get(albumName).push(transformToPhoto(item, def, big));
        } else if(albumName) {
            albums.set(albumName, [transformToPhoto(item, def, big)]);
        }
    });
    return Object.fromEntries(albums);
}
